import React from "react";
import { useQuery } from "react-query3";
import { Button, PageHeader, Space } from "antd";
import { Modal } from "../ui/Modal";
import { CustomColumns, Identity } from "../../types";
import Table from "../ui/Table";
import EditModal from "../ui/EditModal";
import DeleteButton from "../ui/DeleteButton";
import IdentityModal from "components/settings/NewIdentityModal";
import EditorButton from "components/ui/EditorButton";
import { LinkOutlined } from "@ant-design/icons";

export default function Identities() {
  const { data: identities, isLoading, refetch } = useQuery<Identity[]>(
    "/identity",
    {
      enabled: false,
      refetchOnWindowFocus: false,
    }
  );

  function setHidden(record: Identity) {
    return record.name === "System" ? true : false;
  }

  const columns: CustomColumns<Identity>[] = [
    {
      key: "name",
      title: "Name",
      dataIndex: "name",
      editable: false,
    },
    {
      key: "role",
      title: "Role",
      dataIndex: "roleName",
      editable: false,
      render: (text, record) => {
        return text && text !== '' ? text : "Policy Defined"
      }
    },
    {
      width: "fit-content",
      dataIndex: "actions",
      editable: false,
      render: (text, record) => {
        return (
          <Space>
            <Modal>
              <EditModal record={record} allowedWithOneWayGitSync={true} />
            </Modal>
            <EditorButton to={`${record?.id}`} allowedWithOneWayGitSync={true} />
            <DeleteButton hidden={setHidden(record)} resource={record} allowedWithOneWayGitSync={true} />
          </Space>
        );
      },
    },
  ];

  return (
    <PageHeader title="Identities" subTitle="Users or applications that have access to PowerShell Universal." extra={[
      <Button icon={<LinkOutlined />} href="https://docs.powershelluniversal.com/config/security#role-assignment" target="_blank">Documentation</Button>
    ]}>
      <Table
        title="Identities"
        actions={[
          <Modal>
            <IdentityModal />
          </Modal>,
        ]}
        data={identities}
        columns={columns}
        loading={isLoading}
        queryFn={refetch}
      />
    </PageHeader>
  );
}
