import React from "react";
import { Alert, Space, Typography } from "antd";

export default function BuyLicenseAlertBox() {
  return (
    <Alert
      type="info"
      message={
        <Space
          align="center"
          style={{ width: "100%", justifyContent: "space-between" }}
        >
          Authentication requires a license.
          <Typography.Link
            href="https://ironmansoftware.com/powershell-universal/?rel=product"
            target="_blank"
          >
            Buy License.
          </Typography.Link>
        </Space>
      }
    />
  );
}
