import React from "react";
import { AccessControlTypes, Feature, PSUTypes, RoleGuardProps } from "types";
import { Button, ButtonProps } from "antd";
import { Form, FormContentBase } from "./Form/Form";
import { useActionButton } from "components/utils/hooks/useActionButton";
import { useModalContext, ModalOpenButton, ModalContent } from "./Modal";
import useNormalizeData from "components/utils/normalizedRecord";
import BuyLicenseAlertBox from "./BuyLicenseAlertBox";
import useAppStore from "components/context/app/Hooks";


type EditModalProps = {
  action?: string;
  record: PSUTypes;
  schemaName?: string | undefined;
  accessControl?: AccessControlTypes;
  allowedWithOneWayGitSync: boolean;
} & Omit<ButtonProps, "resource"> &
  Omit<RoleGuardProps, "children">;

export default function EditModal({
  record,
  schemaName,
  allowedWithOneWayGitSync,
  ...props
}: EditModalProps) {
  const { getButtonProps } = useActionButton();
  const normalizedRecord = useNormalizeData();
  const { onOk, form } = useModalContext();
  const { licensed } = useAppStore()

  function handleOk() {

    let key = record?.resourceInfo?.schemaName === "tag" ||
      record?.resourceInfo?.schemaName === "role" ||
      record?.resourceInfo?.schemaName === "endpoint" ||
      record?.resourceInfo?.schemaName === "ratelimit" ||
      record?.resourceInfo?.schemaName === "dashboard" ||
      record?.resourceInfo?.schemaName === "schedule" ||
      record?.resourceInfo?.schemaName === "publishedfolder"
      ? record?.resourceInfo?.self
      : record?.resourceInfo?.parent

    if (record?.resourceInfo?.schemaName === "page") {
      key = record?.resourceInfo?.self + "?ignoreContent=true"
    }

    onOk(key,
      "update",
      record
    );
  }

  return (
    <>
      <ModalOpenButton requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Edit} accessControls={props.accessControls} allowedWithOneWayGitSync={allowedWithOneWayGitSync}>
        <Button
          {...props}
          {...getButtonProps({ action: "edit" })}
          action="edit"
        />
      </ModalOpenButton>
      <ModalContent title="Edit Resource" onClickOk={handleOk}>
        <Form schemaName={schemaName || record?.resourceInfo?.schemaName}>
          <FormContentBase
            name="edit_form"
            form={form}
            inEditMode={true}
            preserve={false}
            defaultValues={normalizedRecord(record)}
          />
        </Form>
        {!licensed(Feature.Dashboard) && <BuyLicenseAlertBox />}
      </ModalContent>
    </>
  );
}
