import React, { Fragment } from "react";
import { AccessControlTypes, Identity } from "../../types";
import { Button } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";

export default function NewIdentityModal() {
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/identity", "create", {} as Identity);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={true}>
        <Button children="Add New Identity" />
      </ModalOpenButton>
      <ModalContent title="New Identity" onClickOk={handleOk} formName="create_identity_form">
        <Form schemaName="identity">
          <FormContentBase
            name="create_identity_form"
            form={form}
            preserve={false}
          />
        </Form>
      </ModalContent>
    </Fragment>
  );
}
