import React from "react";
import RoleGuard from "../standalone/role-guard";
import { Button, ButtonProps, Tooltip } from "antd";
import { Link, LinkProps } from "react-router-dom";
import { CodeFilled } from "@ant-design/icons";
import { AccessControlTypes, RoleGuardProps } from "types";

type EditorButtonProps = {
  to: LinkProps["to"];
  allowedWithOneWayGitSync: boolean;
} & Omit<ButtonProps, "resource"> &
  Omit<RoleGuardProps, "children">;

export default function EditorButton({
  to,
  accessControls,
  allowedWithOneWayGitSync,
  ...props
}: EditorButtonProps) {
  return (
    <RoleGuard
      requiredRoles={["Administrator", "Operator"]}
      requiredAccessControls={AccessControlTypes.Edit}
      accessControls={accessControls}
      allowedWithOneWayGitSync={allowedWithOneWayGitSync}
    >
      <Tooltip title="Edit Details">
        <Link to={to}>
          <Button type="text" icon={<CodeFilled />} {...props} />
        </Link>
      </Tooltip>
    </RoleGuard>
  );
}
